.button {
  color: #fff;
  font-family: 'Livvic', sans-serif;
  font-size: 60px;
  line-height: 65px;
  font-weight: 700;
  letter-spacing: 2px;
  text-decoration: none;
  margin-top: 20px;
  border-radius: 20px;
  border: none;
  position: relative;
  z-index: 0;
  cursor: pointer;

  &:after {
    width: 100%;
    height: 25%;
    background-color: #ffb400;
    position: absolute;
    content: '';
    margin: auto;
    left: 0;
    right: 0;
    z-index: -1;

    bottom: 0;
    transition: all 0.2s ease-in-out;

  }

  &:hover:after {
    height: 100%;
    position: absolute;


  }

  @media screen and (min-width: 768px) and (max-width: 1400px) and (max-height: 900px) {
    font-size: 40px;
  }
  @media screen and (min-height: 800px) and (min-width: 768px) and (max-width: 1200px) {
    font-size: 33px;
  }
  @media screen and (max-width: 600px) {
    font-size: 40px;
    text-align: center;
  }
  @media screen and (max-height: 500px) {
    font-size: 20px;

  }
}